import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';

export type StatusType = 'disabled' | 'success' | 'warning' | 'error';

export interface ColorStatusCellData {
    status: StatusType;
}

export interface ColorStatusCellParams
    extends ICellRendererParams<ColorStatusCellData> {
    mapValueToStatus: (data: any) => StatusType;
}

/**
 * @category Table Cell
 * @description A cell component for displaying a color status indicator based on predefined types
 */
@Component({
    tag: 'atui-color-status-cell',
    shadow: false,
})
export class AtuiColorStatusCell implements ICellRendererComp {
    @Element() el: HTMLElement;

    @State() type: StatusType = 'disabled';

    init(params: ColorStatusCellParams): void {
        this.type = params.mapValueToStatus(params.data);
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ColorStatusCellParams): boolean {
        this.type = params.mapValueToStatus(params.data);
        return true;
    }

    render() {
        const statusClasses = {
            error: 'bg-error-base',
            warning: 'bg-warning-base',
            success: 'bg-success-base',
            disabled: 'bg-disabled-light',
        };

        return (
            <Host class="flex h-full items-center">
                <div class={`h-full w-full ${statusClasses[this.type]}`}></div>
            </Host>
        );
    }
}
